import React,{ Component } from 'react'
import { Spin ,Alert} from 'antd'
import './index.less'

class Loading extends Component{

	render(){
		if(this.props.error){
			return(
				<Alert
			     message=",!"
			     description=",ctrl+f5 !!"
			     type="error"
				/>
			)
		}else{
			return (
				<div className="loading">
					<Spin size="large" />	
				</div>
			)
		}
	}
}

export default Loading


