/**
 * Storage 
 * add 
 * del 
 * delAll 
 * get 
 */ 
import {
	message
} from 'antd'

class Storage {
	constructor(){
		if(!window.localStorage){
			message.error('Storage!Chrome')
		}else{
			this.Storage = window.localStorage
		}
	}
	add(data={}){
		for(let key in data){
			this.Storage.setItem(key,data[key])
		}
	}
	del(key){
		this.Storage.removeItem(key)
	}
	delAll(){
		this.Storage.clear()
	}
	get(key){
		return this.Storage.getItem(key)
	}
}

export default new Storage()


