import React,{ Component } from 'react'
import {
	Link
} from 'react-router-dom'
import {
	Row,
	Col
} from 'antd'

class AssetDetails extends Component{
	constructor(props){
		super(props)
		this.state={
			investStayCapital:100,
			creditStayCapital:200,
			investStayInterest:600,
			creditStayInterest:900
		}
	}
	render(){
		return(
			<div className="pandect">
			    <div className="title">
			    	<Row>
			    		<Col span={4}>
							<h1></h1>
			    		</Col>
			    		<Col span={14}>
			    			
			    		</Col>
			    		<Col span={6} className="classfix">
			    			<span></span>
			    			<span><Link to='/account/detail'></Link></span>
			    			<span><Link to='/account/logs'></Link></span>
			    		</Col>
			    	</Row>
			    </div>
			    <div className="body">
			    	<Row>
			    		<Col span={10}>
			    			<div id="pandectBodyTable"></div>
			    		</Col>
			    		<Col span={14}>
			    			<div className="table">
			    				<Row>
			    					<Col span={9}>
			    						<h3></h3>
			    						<p><span className="oval-1"></span></p>
			    						<p><span className="oval-2"></span></p>
			    					</Col>
			    					<Col span={8}>
			    						<h3></h3>
			    						<p>{this.state.investStayCapital}</p>
			    						<p>{this.state.creditStayCapital}</p>
			    					</Col>
			    					<Col span={7}>
			    						<h3></h3>
			    						<p className="origin">{this.state.investStayInterest}</p>
			    						<p className="origin">{this.state.creditStayInterest}</p>
			    					</Col>
			    				</Row>
			    			</div>
			    		</Col>
			    	</Row>
			        <h3 className='footer'></h3>
			    </div>
			</div>
		)
	}
}

export default AssetDetails


