import React,{ Component } from 'react'
import { Link ,withRouter} from 'react-router-dom'
import storage from '@/api/localStorage'


import {
	Row,
	Col,
	Button,
	Tooltip,
	Popconfirm
} from 'antd'



class UserInfo extends Component{
	constructor(props){
		super(props)
		console.log(storage.Storage)
		this.state={
			phone:storage.get('phone'),
			id:(()=>{
					if(storage.get("cardstatus")==="y"){
						return true
					}else{
						return false
					}
				})(),
			bank:storage.get('thirdAccountStatus'),
			email:(()=>{
					if(storage.get("emailstatus")==="y"){
						return true
					}else{
						return false
					}
				})(),
			fundMoney:storage.get('fundMoney'),
			stayAll:0,
			getInterest:0,
			lotteryCount:0,
			imiMoney:0,
			userImg:storage.get('userimg')
		}
	}
	confirm = (e)=>{
		this.props.history.push('/safe')
	}

	render(){
		return(
			<div className="user-info">

                <div className="pandectHeadTop">
                	<Row>
                		<Col span={ 6 }>
                			<div className="user-img">
                    			<img  src={this.state.userImg} alt="userImg"/>
                			</div>
                		</Col>
                		<Col span={ 10 }>
		                    <dl className="pandectHeadData">
		                        <dt>:<span>{this.state.phone}</span> :<span></span></dt>
		                        <dd className="userState">
		                        	<Tooltip title="">
		                        		<span></span>
		                        	</Tooltip>
		                        	<Tooltip title={this.state.id ? "" :""}>
			                        	<span className={this.state.id ? '' : 'off'}></span>
		                        	</Tooltip>
		                        	<Tooltip title={this.state.bank? "" :"" }>
	                        		  <Popconfirm title="" onConfirm={this.confirm} okText="" cancelText=""  placement="bottom" defaultVisible={this.state.bank ?  false : true} >
									    <span className={this.state.bank ? '' : 'off'}></span>
									  </Popconfirm>
		                        	</Tooltip>


		                        	<Tooltip title={this.state.email ? "":""}>
		                        		<span className={this.state.email ? '' : 'off'}></span>
		                        	</Tooltip>
		                        </dd>
		                    </dl>
                		</Col>
                		<Col span={ 8 } className="recharge">
                			<div>
		                		<Button type="primary" className="button-c">
		                			<Link to="/account/recharge" className="recharge-c"></Link>
		                		</Button>
		                		<Button type="default" className="recharge-t">
		                			<Link to="/account/withdraw" ></Link>
		                		</Button>
                			</div>
                		</Col>
                	</Row>
                </div>

                <div className="pandectHeadButton">
                	<Row>
                		<Col span={5}>
                			<p>
                				<span>{this.state.fundMoney}</span>
                			</p>
                			<p></p>
                		</Col>
                		<Col span={5}>
                			<p>
                				<span>{this.state.stayAll}</span>
                			</p>
                			<p></p>
                		</Col>
                		<Col span={5}>
                			<p>
                				<span>{this.state.getInterest}</span>
                			</p>
                			<p></p>
                		</Col>
                		<Col span={5}>
                			<p>
                				<Link to="/privilege/lotteries"><span className="origin">{this.state.lotteryCount}</span></Link>
                			</p>
                			<p></p>
                		</Col>
                		<Col span={4}>
	                		<p>
	                			<span className="origin">{this.state.imiMoney}</span>
	                		</p>
                			<p></p>
                		</Col>
                	</Row>
                </div>
            </div>
		)
	}
}

export default withRouter(UserInfo);


